.abacus1 {
    display: grid;
    grid-template-columns: 6fr 2fr 0.5fr;
    grid-template-rows: 0.5fr 2fr 0.5fr 5fr 0.5fr;
    grid-template-areas:
        '. UE RE'
        '. UC0 RE'
        '. SP RE'
        '. BC0 RE'
        '. BE RE';
    height: 100%;
    width: 100%;
    background: lightgray;
}

.abacus2 {
    display: grid;
    grid-template-columns: 4fr 2fr 2fr 0.5fr;
    grid-template-rows: 1fr 0.5fr 2fr 0.5fr 5fr 0.5fr;
    grid-template-areas:
        '. BH BH BH'
        '. UE UE RE'
        '. UC1 UC0 RE'
        '. SP SP RE'
        '. BC1 BC0 RE'
        '. BE BE RE';
    height: 100%;
    width: 100%;
    background: lightgray;
}

.reset {
           
    border-top: 5px solid black;
    border-left: 0;
    border-right: 0;
    border-bottom: 0;
    
    height: 40px;
    width: 80px;
    background: rgba(0, 0, 0, 1);
    background: -moz-linear-gradient(left, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(0, 0, 0, 1) 100%);
    background: -webkit-gradient(left top, right top, color-stop(0%, rgba(0, 0, 0, 1)), color-stop(50%, rgba(255, 255, 255, 1)), color-stop(100%, rgba(0, 0, 0, 1)));
    background: -webkit-linear-gradient(left, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(0, 0, 0, 1) 100%);
    background: -o-linear-gradient(left, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(0, 0, 0, 1) 100%);
    background: -ms-linear-gradient(left, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(0, 0, 0, 1) 100%);
    background: linear-gradient(to right, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(0, 0, 0, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#000000', endColorstr='#000000', GradientType=1);
}

.reset_clicked {


    -ms-transform: matrix(1, 0, 0, 0.2, 0, 16);
    /* IE 9 */
    -webkit-transform: matrix(1, 0, 0, 0.2, 0, 16);
    /* Safari */
    transform: matrix(1, 0, 0, 0.2, 0, 16);
}
