.bead {
    background-color: bisque;
    border-radius: 43%;
    background: radial-gradient(at 25px 10px, rgb(255, 228, 196), rgb(195, 168, 136));
}

.bead_upper {
    width: 100%;
    height: 50%;

}

.bead_bottom {
    width: 100%;
    height: 20%;
}

.cleartransform {
    transform: translateY('0px')
}
